@import '../../styles/mixins';

.SearchPaymentsWrapper {
  position: relative;
  @include XS {
    height: 70px;
  }
}

.SearchPayments {
  width: 713px;
  margin: 18px auto 18px 111px;
  display: flex;
  align-items: center;
  justify-content: center;
  @include L {
    margin: 18px auto;
    justify-content: space-around;
    height: 20px;
  }
  @include M {
    width: 540px;
  }
  @include S {
    width: 370px;
    flex-direction: column;
    align-items: flex-start;
    flex-wrap: wrap;
    height: 60px;
    margin: 8px auto;
  }
  @include XS {
    width: 275px;
    margin: 5px auto;
  }
}

.suppliers {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-right: 9px;
  border: 1px solid #dcdcdc;
  border-radius: 4px;
  background-color:  #fdfdfd;
  width: 52%;
  height: 30px;
  @include L {
    height: 100%;
    margin: 0;
  }
  @include S {
    height: 20px;
  }
}

.suppliersSearch {
  border: none;
  outline: none;
  width: 100%;
  height: 85%;
  padding-left: 11px;
  &::placeholder {
    font-family: Helvetica, sans-serif;
    color:  #999999;
    font-size: 0.75rem;
    font-weight: 400;
    @include XS {
      font-size: 0.625rem;
    }
  }
}

.rating {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 9px;
  border-radius: 4px;
  background-color:  #eaeced;
  width: 22.5%;
  @include L {
    height: 28px;
    margin: 0;
  }
  @include S {
    height: 25px;
    width: 122px;
  }
}

.ratingSelect {
  font: 400 0.688rem Arial, sans-serif;
  padding: 3px 5px 2px 9px;
  margin: 6px;
  color: #000000;
  text-align: left;
  width: 100%;
  height: 20px;
  background-image: linear-gradient(to bottom, #FFF, #F1F1F1);
  border: 1px solid #9C9C9C;
  border-radius: 4px;
  cursor: pointer;
  -webkit-appearance: menulist;
  @include L {
    width: 93%;
    height: 18px;
    padding: 1px;
    margin: 1px;
    font-size: 0.5625rem;
  }
  @include S {
  }
}

.ratingOption {
  padding: 5px 0 2px 5px;
  font-family: Arial, sans-serif;
  color: #111111;
  font-size: 0.688rem;
  font-weight: 400;
  text-align: left;
  @include S {
    font-size: 0.5625rem;
    padding: 3px 0 1px 3px;
  }
}

.resetButton {
  align-self: flex-end;
  width: 74px;
  height: 30px;
  margin-right: 9px;
  border: none;
  border-radius: 4px;
  background-image: linear-gradient(to bottom, #EFEFEF, #C7C7C7);
  font-family: Helvetica, sans-serif;
  color:  #494949;
  font-size: 0.75rem;
  font-weight: 700;
  line-height: 1.167;
  cursor: pointer;
  @include L {
    width: 50px;
    height: 20px;
    font-size: 0.5625rem;
    font-weight: 400;
    margin: 0;
  }
  @include S {
    width: 40px;
    height: 20px;
  }
  &:hover {
    transform: scale(1.1);
    transition-duration: 0.2s;
  }
}

.searchButton {
  @extend .resetButton;
  width: 80px;
  margin: 0;
  background-image: linear-gradient(to bottom, #689FF3, #2278F0);
  color:  #fdfdfd;
  @include L {
    width: 53px;
  }
  @include S {
    width: 40px;
  }
}

.error {
  position: absolute;
  bottom: -16px;
  left: 0;
  right: 0;
  font-family: Helvetica, sans-serif;
  text-align: center;
  color: red;
  font-size: 0.75rem;
  font-weight: 700;
  @include L {
    font-size: 0.6875rem;
    font-weight: 400;
  }
  @include S {
    font-size: 0.625rem;
    bottom: -7px;
  }
  @include XS {
    font-size: 0.5625rem;
    bottom: 2px;
  }
}


