@import '../../styles/mixins';

.App {
  width: 1300px;
  height: 800px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  @include XL {
    width: 1000px;
    height: auto;
    margin: 20px auto;
  }
  @include L {
    width: 800px;
  }
  @include M {
    width: 560px;
    margin: 12px auto;
  }
  @include S {
    width: 390px;
  }
  @include XS {
    width: 285px;
  }
}

.paymentsSectionWrapper {
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.paymentsSection {
  border-radius: 3px;
  background-color:  #fdfdfd;
  box-shadow: 0 0 2px 1px #a1a1a1, 0 0 2px 8px #cecece;
}


