@import '../../styles/mixins';

.tableBodyCell {
  text-align: center;
  vertical-align: middle;
  border: 1px solid #e0e0e0;
  font-family: Helvetica, sans-serif;
  font-size: 0.75rem;
  font-weight: 400;
  color: #555555;
  &:before {
    @include M {
      position: absolute;
      top: 6px;
      left: 6px;
      width: 45%;
      padding-right: 10px;
      white-space: nowrap;
      content: attr(data-label);
      font: 700 0.75rem Helvetica, sans-serif;
    }
    @include S {
      width: 25%;
      font-size: 0.6875rem;
    }
    @include XS {
      width: 22%;
      font-size: 0.6875rem;
      padding-right: 6px;
      top: 5px;
      left: 3px;
      line-height: 1.1;
    }
  }
  @include M {
    display: flex;
    align-items: center;
    border-top: none;
    position: relative;
    padding-left: 50%;
    text-align: left;
    height: 25%;
  }
  @include S {
    padding-left: 28%;
  }
  @include XS {
    padding-left: 23%;
  }
}

.blueCircle {
  display: inline-block;
  border: 1px solid #7C7C7C;
  border-radius: 50%;
  background-image: linear-gradient(to bottom, #65C0EA, #4C95DC);
  width: 26px;
  height: 26px;
  margin-right: 4px;
  font-family: 'Source Sans Pro', sans-serif;
  color:   #fdfdfd;
  font-size: 0.938rem;
  font-weight: 900;
  text-align: center;
  line-height: 1.6;
  @include L {
    width: 24px;
    height: 24px;
    margin-right: 3px;
    font-size: 0.8125rem;
    font-weight: 400;
  }
  @include M {
    width: 17px;
    height: 17px;
    margin-right: 2px;
    line-height: 1.4;
    font-size: 0.6875rem;
  }
  @include XS {
    width: 15px;
    height: 15px;
    line-height: 1.2;
    font-size: 0.625rem;
  }
}

.greyCircle {
  @extend .blueCircle;
  background-image: linear-gradient(to bottom, #FFFFFF, #E2E4E4);
  color:  #666666;
}


