@import '../../styles/mixins';

.paginationNav {
  margin: 19px auto;
  @include S {
    margin: 10px auto;
  }
}

.paginationList {
  width: 50vw;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  @include L {
    width: 750px;
  }
  @include M {
    width: 540px;
  }
  @include S {
    width: 370px;
  }
  @include XS {
    width: 275px;
  }
}

.paginationItem {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 26px;
  height: 25px;
  margin: 0 2px;
  border-radius: 3px;
  border: 1px solid #B9B9B9;
  background-image: linear-gradient(to bottom, #FBFBFB, #EAEAEA);
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.12);
  cursor: pointer;
  color:  #717171;
  @include L {
    width: 21px;
    height: 20px;
  }
  @include S {
    width: 18px;
    height: 17px;
  }
  &:hover {
    background-image: linear-gradient(to bottom, darken(#FBFBFB,10%), darken(#EAEAEA, 10%));
    transition-duration: 0.2s;
  }
}

.paginationItem--active {
  @extend .paginationItem;
  border: 1px solid #484848;
  background-image: linear-gradient(to bottom, #676767, #606060);
  color: #f2f2f2;
  &:hover {
    background-image: linear-gradient(to bottom, darken(#676767,10%), darken(#606060, 10%));
    transition-duration: 0.2s;
  }
}

.paginationItem--disabled {
  @extend .paginationItem;
  pointer-events: none;
  cursor: default;
  background-image: linear-gradient(to bottom, lighten(#FBFBFB, 10%), lighten(#EAEAEA, 10%));
  color: lighten(#717171, 10%);

}


.paginationLink {
  text-decoration: none;
  font-family: Helvetica, sans-serif;
  color: inherit;
  font-size: 0.75rem;
  font-weight: 700;
  text-shadow: 0 1px 0 #ffffff;
  @include L {
    font-size: 0.6875rem;
    font-weight: 400;
  }
  @include S {
    font-size: 0.625rem;
  }
  &:hover {
   color: inherit;
  }
  &:focus {
    text-decoration: none;
  }
}


