@import '../../styles/mixins';

.PaymentsTable {
  width: 940px;
  margin: 0 auto;
  @include L {
    width: 750px;
  }
  @include M {
    width: 540px;
    display: block;
  }
  @include S {
    width: 370px;
  }
  @include XS {
    width: 275px;
  }
}

.tableHead {
  height: 60px;
  background-image: linear-gradient(to bottom, #E9EBEC, #E6E8E8);
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.05);
  &::before {
    @include M {
      font: 700 0.875rem Helvetica, sans-serif;
      content: attr(data-label);
      text-transform: uppercase;
      padding: 2px;
    }
    @include S {
      font-size: 0.8125rem;
    }
    @include XS {
      font-size: 0.75rem;
    }
  }
  @include M {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 25px;
    border: 1px solid #dcdcdc;
    border-top-right-radius: 4px;
    border-top-left-radius: 4px;
  }
  @include S {
    height: 20px;
  }
}

.tableHeadRow {
  height: 60px;
  @include L {
    height: 50px;
  }
  @include M {
    display: block;
    position: absolute;
    top: -9999px;
    left: -9999px;
    border: 1px solid black;
  }
}

.tableHeadCell {
  border-top: 1px solid #dcdcdc;
  border-bottom: 1px solid #dcdcdc;
  font-family: Helvetica, sans-serif;
  font-size: 0.75rem;
  font-weight: 700;
  vertical-align: middle;
  color: #5A5A5A;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.6);
  @include L {
    font-size: 0.6875rem;
  }
  @include M {
    display: block;
  }
  &:first-child {
    text-align: left;
    padding-left: 29px;
    border-top-left-radius: 4px;
    width: 59%;
  }
  &:nth-child(2) {
    width: 19%;
  }
  &:nth-child(3) {
    width: 11%;
  }
  &:last-child {
    border-top-right-radius: 4px;
    width: 11%;
  }
}

.tableBody {
  @include M {
    display: block;
  }
}

.tableBodyRow {
  background-color: #FAFAFA;
  height: 50px;
  cursor: pointer;
  @include L {
    height: 42px;
  }
  @include M {
    height: 100px;
    display: block;
  }
  @include S {
    height: 90px;
  }
  &:nth-child(even) {
    background-image: linear-gradient(to bottom, #F5F5F5, #F3F3F3);
  }
  &:last-child {
    .tableBodyCell:first-child {
      border-bottom-left-radius: 4px;
    }
    .tableBodyCell:last-child {
      border-bottom-right-radius: 4px;
      @include M {
        border-bottom-right-radius: 4px;
        border-bottom-left-radius: 4px;
      }
    }
  }
}

.tableBodyCell {
  text-align: center;
  vertical-align: middle;
  border: 1px solid #E1E1E1;
  font-family: Helvetica, sans-serif;
  font-size: 0.75rem;
  font-weight: 400;
  color:  #555555;
  &::before {
    @include M {
      position: absolute;
      font: 700 0.75rem Helvetica, sans-serif;
      top: 6px;
      left: 6px;
      width: 45%;
      padding-right: 10px;
      white-space: nowrap;
      content: attr(data-label);
    }
    @include S {
      width: 25%;
      font-size: 0.6875rem;
    }
    @include XS {
      width: 22%;
      font-size: 0.6875rem;
      padding-right: 6px;
      left: 3px;
      top: 5px;
      line-height: 1.1;
     }
  }
  @include L {
    font-size: 0.6875rem;
  }
  @include M {
    display: flex;
    align-items: center;
    border-top: none;
    position: relative;
    padding-left: 50%;
    text-align: left;
    height: 25%;
  }
  @include S {
    padding-left: 28%;
    font-size: 0.625rem;
  }
  @include XS {
    padding-left: 23%;
  }
  &:first-child {
    text-align: left;
    padding-left: 29px;
    @include M {
      padding-left: 50%;
    }
    @include S {
      padding-left: 28%;
    }
    @include XS {
      padding-left: 23%;
    }
  }
  &:last-child {
    @include M {
      border-bottom: 1.5px solid #E1E1E1;
    }
  }
}



