@import '../../styles/mixins';

.Header {
  width: 940px;
  height: 112px;
  margin: 27px auto 17px;
  text-align: center;
  position: relative;
  @include L {
    width: 750px;
  }
  @include M {
    width: 540px;
    height: 90px;
    margin: 21px auto 13px;
  }
  @include S {
    width: 380px;
    height: 80px;
    margin: 15px auto 10px;
  }
  @include XS {
    width: 273px;
    margin: 10px auto 7px;
  }
}

.heading {
  font-family: 'Source Sans Pro', sans-serif;;
  color:  #2288b7;
  font-size: 3.75rem;
  font-weight: 300;
  margin-bottom: 11px;
  padding-right: 24px;
  @include L {
    font-size: 3.125rem;
    padding-right: 0;
  }
  @include M {
    font-size: 2.8125rem;
    margin-bottom: 8px;
  }
  @include S {
    font-size: 2.375rem;
  }
  @include XS {
    font-size: 1.6875rem;
    margin-bottom: 5px;
  }
}

.subheading {
  font-family: 'Source Sans Pro', sans-serif;
  color: #3a3b3b;
  font-size: 1.063rem;
  font-weight: 400;
  padding-right: 22px;
  @include L {
    font-size: 0.9375rem;
    padding-right: 0;
  }
  @include M {
    font-size: 0.875rem;
  }
  @include S {
    font-size: 0.8125rem;
  }
  @include XS {
    font-size: 0.75rem;
  }
}

.hr {
  width: 100%;
  height: 0;
  margin: 0;
  position: absolute;
  left: 0;
  bottom: 0;
  border: 0;
  border-bottom: 1px solid #dddddd;
}




